/* Base */
@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
   ========================================================================== */
/**
 * Remove the margin in all browsers.
 */
body {
  margin: 0;
}

/**
 * Render the `main` element consistently in IE.
 */
main {
  display: block;
}

/**
 * Correct the font size and margin on `h1` elements within `section` and
 * `article` contexts in Chrome, Firefox, and Safari.
 */
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
   ========================================================================== */
/**
 * 1. Add the correct box sizing in Firefox.
 * 2. Show the overflow in Edge and IE.
 */
hr {
  box-sizing: content-box; /* 1 */
  height: 0; /* 1 */
  overflow: visible; /* 2 */
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/* Text-level semantics
   ========================================================================== */
/**
 * Remove the gray background on active links in IE 10.
 */
a {
  background-color: transparent;
}

/**
 * 1. Remove the bottom border in Chrome 57-
 * 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.
 */
abbr[title] {
  border-bottom: none; /* 1 */
  text-decoration: underline; /* 2 */
  text-decoration: underline dotted; /* 2 */
}

/**
 * Add the correct font weight in Chrome, Edge, and Safari.
 */
b,
strong {
  font-weight: bolder;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/**
 * Add the correct font size in all browsers.
 */
small {
  font-size: 80%;
}

/**
 * Prevent `sub` and `sup` elements from affecting the line height in
 * all browsers.
 */
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

/* Embedded content
   ========================================================================== */
/**
 * Remove the border on images inside links in IE 10.
 */
img {
  border-style: none;
}

/* Forms
   ========================================================================== */
/**
 * 1. Change the font styles in all browsers.
 * 2. Remove the margin in Firefox and Safari.
 */
button,
input,
optgroup,
select,
textarea {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
}

/**
 * Show the overflow in IE.
 * 1. Show the overflow in Edge.
 */
button,
input { /* 1 */
  overflow: visible;
}

/**
 * Remove the inheritance of text transform in Edge, Firefox, and IE.
 * 1. Remove the inheritance of text transform in Firefox.
 */
button,
select { /* 1 */
  text-transform: none;
}

/**
 * Correct the inability to style clickable types in iOS and Safari.
 */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/**
 * Remove the inner border and padding in Firefox.
 */
button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
 * Restore the focus styles unset by the previous rule.
 */
button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
 * Correct the padding in Firefox.
 */
fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
 * 1. Correct the text wrapping in Edge and IE.
 * 2. Correct the color inheritance from `fieldset` elements in IE.
 * 3. Remove the padding so developers are not caught out when they zero out
 *    `fieldset` elements in all browsers.
 */
legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  padding: 0; /* 3 */
  white-space: normal; /* 1 */
}

/**
 * Add the correct vertical alignment in Chrome, Firefox, and Opera.
 */
progress {
  vertical-align: baseline;
}

/**
 * Remove the default vertical scrollbar in IE 10+.
 */
textarea {
  overflow: auto;
}

/**
 * 1. Add the correct box sizing in IE 10.
 * 2. Remove the padding in IE 10.
 */
[type=checkbox],
[type=radio] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
}

/**
 * Correct the cursor style of increment and decrement buttons in Chrome.
 */
[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

/**
 * 1. Correct the odd appearance in Chrome and Safari.
 * 2. Correct the outline style in Safari.
 */
[type=search] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
 * Remove the inner padding in Chrome and Safari on macOS.
 */
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * 1. Correct the inability to style clickable types in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */
::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/* Interactive
   ========================================================================== */
/*
 * Add the correct display in Edge, IE 10+, and Firefox.
 */
details {
  display: block;
}

/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}

/* Misc
   ========================================================================== */
/**
 * Add the correct display in IE 10+.
 */
template {
  display: none;
}

/**
 * Add the correct display in IE 10.
 */
[hidden] {
  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
}

p {
  color: #0d1821;
  font-size: 2rem;
  font-weight: 300;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img, picture {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 114rem;
}

.alinear-derecha {
  display: flex;
  justify-content: end;
}

.comodin {
  margin-bottom: 6rem;
}

.btn-amarillo-block {
  background-color: #ffbe00;
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  border: none;
}
.btn-amarillo-block:hover {
  background-color: #e6ab00;
  cursor: pointer;
}

.btn-amarillo {
  background-color: #ffbe00;
  display: inline-block;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  border: none;
}
.btn-amarillo:hover {
  background-color: #e6ab00;
  cursor: pointer;
}

.btn-verde {
  background-color: #71B100;
  display: inline-block;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  border: none;
}
.btn-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.ver-todas {
  display: flex;
  justify-content: center;
  margin: 3rem 0rem;
}
@media (min-width: 768px) {
  .ver-todas {
    justify-content: end;
  }
}

/* Layout */
.webp .inicio {
  background-image: url("../img/header.webp");
}

.no-webp .inicio {
  background-image: url("../img/header.jpg");
}

.header {
  background-color: #333333;
  padding: 0rem;
}

.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .inicio {
    min-height: 65rem;
  }
}
@media (min-width: 768px) {
  .inicio .contenido-header {
    min-height: 60rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: center;
  color: #FFFFFF;
  font-weight: 400;
  max-width: 60rem;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    text-align: left;
  }
}

.contenido-header {
  max-width: 136rem;
  padding: 1.5rem;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .logo-header {
  max-width: 100%;
  width: 40rem;
  height: auto;
}

.navegacion-header {
  margin: 3rem;
}
.navegacion-header a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2rem;
  font-weight: 300;
  display: block; /* vista para mmovil */
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion-header {
    display: flex;
    gap: 2.5rem;
    margin: 0rem;
  }
}

@media (min-width: 768px) {
  .iconos_nosotros {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.icono {
  text-align: center;
  padding: 3rem;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.anuncio {
  background-color: #eeeeee;
  border: 0.5px solid #c8c8c8;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .anuncio {
    margin: 0;
  }
}
.anuncio:last-of-type {
  margin-bottom: 0rem;
}
.anuncio h3 {
  font-weight: 400;
}
.anuncio h3, .anuncio p {
  margin: 0;
}

.contenido-anuncio {
  padding: 2rem;
  text-align: center;
}

.precio {
  color: #71B100;
  padding: 1rem;
  font-weight: 400;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  justify-content: space-around;
  padding: 0rem;
  list-style: none;
}
.iconos-caracteristicas li {
  display: flex;
  column-gap: 0.7rem;
}

.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp);
}

.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg);
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 8rem 0;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.info h2 {
  font-size: 4rem;
  font-weight: 700;
  color: #FFFFFF;
}
.info p {
  font-weight: 300;
  color: #FFFFFF;
}

@media (min-width: 992px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 10rem;
  }
}
.entrada-blog {
  margin-bottom: 10rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    margin-bottom: 5rem;
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 7rem;
}

.texto-entrada {
  margin-top: 1.5rem;
}
@media (min-width: 768px) {
  .texto-entrada {
    margin: 0;
  }
}
.texto-entrada a {
  color: #0d1821;
}
.texto-entrada h4 {
  margin: 0;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.4rem;
  background: #71B100;
  margin-top: 1rem;
}
.texto-entrada .spafecha {
  margin-right: 1rem;
}

.informacion-meta span {
  color: #ffbe00;
  padding: 0.3rem;
}

.testimonial {
  background-color: #71B100;
  font-size: 2rem;
  padding: 0rem 1rem;
  margin: 1rem;
  color: #FFFFFF;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
@media (min-width: 992px) {
  .testimonial {
    height: 40rem;
  }
}
.testimonial blockquote {
  padding-left: 3rem;
  position: relative;
  font-weight: 300;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/svg.svg);
  background-repeat: no-repeat;
  width: 3rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.footer {
  background-color: #333333;
  align-items: center;
  margin-bottom: 0;
}
@media (min-width: 992px) {
  .footer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-flow: column;
  }
  .footer .copyright {
    grid-column: 1/2;
  }
  .footer .contenido-footer {
    grid-column: 2/3;
  }
}

.navegacion-footer {
  padding: 3rem;
}
.navegacion-footer a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2rem;
  font-weight: 300;
  display: block; /* vista para mmovil */
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion-footer {
    display: flex;
    justify-content: space-around;
    gap: 2.5rem;
    margin: 0rem;
  }
}
@media (min-width: 992px) {
  .navegacion-footer {
    justify-content: end;
  }
}

.copyright {
  margin: 0;
  padding-bottom: 1rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 300;
}
@media (min-width: 992px) {
  .copyright {
    padding-bottom: 0rem;
    text-align: start;
    padding-left: 3rem;
  }
}

/* Internas */
.contenido-nosotros {
  margin: 5rem 0;
}
@media (min-width: 992px) {
  .contenido-nosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.texto-nosotros blockquote {
  font-size: 2.5rem;
  font-weight: 900;
  text-align: center;
  margin: 0rem 0rem 1.5rem 0rem;
}
@media (min-width: 992px) {
  .texto-nosotros blockquote {
    font-size: 4rem;
  }
}

.texto-nosotros {
  margin: 2rem 0rem;
}
@media (min-width: 992px) {
  .texto-nosotros {
    margin: 0rem;
  }
}
.texto-nosotros p {
  line-height: 1.7;
  letter-spacing: 0.5px;
}

.webp .contacto-img {
  background-image: url("../img/destacadas/destacada3.webp");
}

.no-webp .contacto-img {
  background-image: url("../img/destacadas/destacada3.jpg");
}

.contacto-img {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .contacto-img {
    min-height: 65rem;
  }
}
@media (min-width: 768px) {
  .contacto-img .contenido-contacto {
    min-height: 60rem;
  }
}

.contenido-contacto {
  padding: 5rem 0;
}
.contenido-contacto h1 {
  color: #FFFFFF;
  margin: 0;
  padding-bottom: 3rem;
  font-weight: 700;
  font-size: 5rem;
}

.formulario {
  background-color: rgba(255, 255, 255, 0.8);
  padding: 10px;
}
.formulario fieldset {
  margin-bottom: 2rem;
}
.formulario p {
  font-size: 1.4rem;
  margin: 0rem 0rem 0.5rem 0rem;
  font-weight: 400;
}
.formulario legend {
  font-size: 3rem;
  font-weight: 700;
}
.formulario label {
  font-weight: 700;
  display: block;
}
.formulario input:not([type=submit], [type=number]), .formulario textarea {
  padding: 1rem;
  display: block;
  width: 100%;
  border: none;
  border-radius: 1rem;
  margin: 1rem 0;
}
.formulario input[type=number], .formulario select {
  padding: 0.5rem;
  margin: 1rem 0;
  border: none;
  border-radius: 0.5rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 22rem;
  margin-bottom: 1rem;
}
.formulario .forma-contacto input {
  margin-right: 2rem;
}
.formulario .enviar-formulario {
  display: flex;
  justify-content: end;
  margin: 4rem 1rem 1rem 2rem;
}